import React, {useState} from 'react'
import Video from '../../video/video2.mp4'
import {Button} from '../ButtonElement';
import {HeroContainer, HeroBg, VideoBg, HeroContent, HeroH1, HeroP, HeroBtnWrapper, ArrowForward, ArrowRight, HeroBtnLink} from './HeroElements'
const HeroSection = () => {
    const [hover, setHover]=useState(false)
    const onHover=()=>{
        setHover(!hover)
    }
    

    return (
        <HeroContainer id="home">
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video/mp4'/>
            </HeroBg>
            <HeroContent>
                <HeroH1> Tervetuloa Lauri Pelinin portfolioon </HeroH1>
                <HeroP>
                    Täältä löydät linkit projekteihini, sekä CV:ni. Rakensin tämän sivun React.js, Node.js ja Visual Studio Code avulla.
                </HeroP>
                <HeroBtnWrapper>
                    <HeroBtnLink to='signin' onMouseEnter={onHover} onMouseLeave={onHover}
                    primary="true"
                    dark="true" >
                        Linkit {hover ? <ArrowForward /> : <ArrowRight/>}
                    </HeroBtnLink>
                </HeroBtnWrapper>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
