import React from 'react';
import {FaBars} from 'react-icons/fa';
import {Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItem, NavLinks, NavBtn, NavBtnLink} from './NavbarElements';
// <NavItem> toiminnallisuudet lisäsivuille
//<NavLinks to= 'services'>Pööpö</NavLinks>
//</NavItem>
//<NavItem>
//    <NavLinks to= 'signup'>Lääpä</NavLinks>
//</NavItem>

const Navbar = ({toggle}) => {
    return (
        <>
        <Nav>
            <NavbarContainer>
                <NavLogo to='/'>LP</NavLogo>
                <MobileIcon onClick={toggle}>
                    <FaBars/>
                </MobileIcon>
                <NavMenu>
                    <NavItem>
                        <NavLinks to= 'about'>Projektit</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to= 'discover'>CV</NavLinks>
                    </NavItem>
                    
                </NavMenu>
                <NavBtn>
                    <NavBtnLink to="/signin">Ota yhteyttä</NavBtnLink>
                </NavBtn>
            </NavbarContainer>
        </Nav>
        </>
    );
};

export default Navbar;
