import React from 'react'
import {SidebarContainer, Icon, CloseIcon, SidebarWrapper, SidebarMenu, SidebarLink, SidebarRoute, SideBtnWrap} from './SidebarElements';
// tekstikenttien projektit ymv oltava samat sidebar indexjs sekä navbar indexjs
// <SidebarLink to="services"onClick={toggle}>Pööpö</SidebarLink> toiminnallisuudet valikon lisäsivuille
// <SidebarLink to="signup"onClick={toggle}>Pääpä</SidebarLink>
const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarWrapper>
                <SidebarMenu>
                    <SidebarLink to='about'onClick={toggle}>Projektit</SidebarLink>
                    <SidebarLink to='discover'onClick={toggle}>CV</SidebarLink>
                    
                </SidebarMenu>
                <SideBtnWrap>
                    <SidebarRoute to="/signin" >Ota yhteyttä</SidebarRoute>
                </SideBtnWrap>
            </SidebarWrapper>
        </SidebarContainer>
    );
};
export default Sidebar
